<template>
    <div class="CollectioncontentArea">
        <div class="content">
            <div class="test-title">
                <div class="test-icon">
                    <span v-if="AnswerGroups.AnswerLength.length===4">选择题</span>
                    <span v-else>判断题</span>
                </div>

                <span class="test-question">
                    {{AnswerGroups.question}}
                </span>
            </div>

            <div v-if="AnswerGroups.Answerurl" class="test-img">
                <img :src="AnswerGroups.Answerurl">
            </div>
            <div class="answerb">
                <li v-for="(item, index) in AnswerGroups.AnswerLength" :key="index">
                    <div class="option">
                        <span @click="choseAnswer(index+1)" class="option-item">{{indexAz[index+1]}}</span>
                        <span class="chose-question">{{item}}</span>
                    </div>
                </li>
            </div>
        </div>
    </div>
</template>

<script>
import { setTimeout } from 'timers';
    export default {
        name: 'CollectioncontentArea',
        props: ['AnswerGroups', 'nowdata'],
        data() {
            return {
                CollectionQuestion: window.localStorage.CollectionQuestion,
                indexAz: {
                    1: 'A',
                    2: 'B',
                    3: 'C',
                    4: 'D'
                },
            }
        },
        created() {
            // 进行清空
            this.$emit('changeData', 'empty')
            var CollectionQuestion = window.localStorage.CollectionQuestion
            //获取问题
            this.$emit('changeData', 'question', this.AnswerGroups.Driveanswer.result[CollectionQuestion[this
                .AnswerGroups.Answerno] - 1].question)
            //获取图片
            this.$emit('changeData', 'Answerurl', this.AnswerGroups.Driveanswer.result[CollectionQuestion[this
                .AnswerGroups.Answerno] - 1].url)
            //获取ID
            this.$emit('changeData', 'AnswerId', this.AnswerGroups.Driveanswer.result[CollectionQuestion[this
                .AnswerGroups.Answerno] - 1].id)
            //获取正确答案
            this.$emit('changeData', 'Answer', this.AnswerGroups.Driveanswer.result[CollectionQuestion[this
                .AnswerGroups.Answerno] - 1].answer)
            for (let key in this.AnswerGroups.Driveanswer.result[CollectionQuestion[this.AnswerGroups.Answerno] - 1]) {
                if (key.match('item')) {
                    this.$emit('changeData', 'AnswerLength', this.AnswerGroups.Driveanswer.result[CollectionQuestion[
                        this.AnswerGroups.Answerno] - 1][key])
                }
            }
        },
        methods: {
            choseAnswer(index) {
                this.$emit('changeData', 'Answerno')
                if (index == this.AnswerGroups.Answer) {
                    console.log('正确')
                    this.$emit('changeData', 'Answertrue')
                } else {
                    console.log('错误')
                    this.$emit('changeData', 'Answerfalse')
                }
            }
        },
        watch: {
            "AnswerGroups.Answerno"() {
                if (this.AnswerGroups.Answerno>=this.CollectionQuestion.split(',').length) {
                    setTimeout(()=>{
                    this.$router.push('/collection')
                    },500)
                    alert('答题完成')
                    return
                }
                // 进行清空
                this.$emit('changeData', 'empty')
                this.$emit('changeData', 'question', this.AnswerGroups.Driveanswer.result[this.CollectionQuestion.split(
                    ',')[this.AnswerGroups.Answerno] - 1].question)
                this.$emit('changeData', 'Answerurl', this.AnswerGroups.Driveanswer.result[this.CollectionQuestion
                    .split(',')[this.AnswerGroups.Answerno] - 1].url)
                this.$emit('changeData', 'AnswerId', this.AnswerGroups.Driveanswer.result[this.CollectionQuestion.split(
                    ',')[this.AnswerGroups.Answerno] - 1].id)
                this.$emit('changeData', 'Answer', this.AnswerGroups.Driveanswer.result[this.CollectionQuestion.split(
                    ',')[this.AnswerGroups.Answerno] - 1].answer)
                for (let key in this.AnswerGroups.Driveanswer.result[this.CollectionQuestion.split(',')[this
                        .AnswerGroups.Answerno] - 1]) {
                    if (key.match('item')) {
                        this.$emit('changeData', 'AnswerLength', this.AnswerGroups.Driveanswer.result[this
                            .CollectionQuestion.split(',')[this.AnswerGroups.Answerno] - 1][key])
                    }
                }
            }
        },

    }
</script>


<style scope lang="less">
    .CollectioncontentArea {
        margin-bottom: 5rem;
        display: flex;
        flex-wrap: wrap;

        .content {
            padding: 2rem;
            width: 100%;

            .test-title {
                font-size: 1.8rem;

                .test-icon {
                    display: inline-block;
                    font-size: 1.2rem;
                    background: yellowgreen;
                    border-top-left-radius: 20rem;
                    border-bottom-left-radius: 20rem;
                    border-top-right-radius: 20rem;
                    padding: 0.5rem 1rem;
                    margin-right: 1rem;

                    span {
                        color: white;
                    }
                }
            }

            .test-img {
                width: 100%;
                display: flex;
                justify-content: center;
                margin: 2rem 0;

                img {
                    max-width: 100%;
                }
            }

            .answerb {
                display: flex;
                flex-wrap: wrap;
                margin-top: 3rem;

                li {
                    width: 100%;
                    margin: 1rem 0;

                    .option {
                        font-size: 2rem;

                        .option-item {
                            padding: 0.5rem;
                            border-radius: 50%;
                            box-shadow: 0 0 0.5rem 0.2rem lightgray;
                            cursor: pointer;
                        }

                        .chose-question {
                            line-height: 2;
                            margin-left: 2rem;
                        }
                    }
                }
            }
        }
    }
</style>